<template>
  <div style="width:600px;height:1024px">
    <div class="posterBg">
      <img :src="essaySrc" width="100%" height="100%" alt="" />
    </div>
    <div class="front">
      <div class="tab-top">
        <img
          :src="goBackSrc"
          width="3.3rem"
          height="3.3rem"
          alt=""
          @click="goBack"
        />
        <span style="font-family: regular;font-weight: 400;"
          >{{ timeValue }}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      timeValue: "",
      goBackSrc: require("@/assets/goBack.png"),
      essaySrc: require("@/assets/essay.jpg"),
    };
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
    },
    goBack() {
      this.$router.push({ path: "/" });
    },
  },
};
</script>
