<template>
  <div style="width:600px;height:1024px">
    <div class="background">
      <img :src="imgSrcTow" width="100%" height="100%" alt="" />
    </div>
    <div class="front">
      <navigationBar
        :timeValue="timeValue"
        @operationChangeVlaue="operationChangeVlaue"
        v-if="operationShow"
      ></navigationBar>
      <div class="tab-top">
        <van-icon
          @click="operationShow = true"
          name="wap-nav"
          style="align-items: center;justify-content: center;display: flex;"
        />
        <span style="font-family: regular;font-weight: 400;"
          >{{ timeValue }}
        </span>
        <div
          style="position: absolute;top: 53rem;left: 2.7rem; width: 512px;height: 3.3rem"
          @click="getDetails"
        ></div>
        <div
          style="position: absolute;top:59.4rem;left: 13.5rem;width: 3rem;height: 2rem"
          @click="dataMoChang"
        ></div>
      </div>
    <div class="day" style="position: absolute;top: 6rem;left: 3rem;">
      <div class="date">{{ date }}</div>
      <div class="fg"><span>/</span></div>
      <div class="ez">
        <span>{{ month }}</span>
        <span>{{ year }}</span>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import navigationBar from "../components/navigationBar.vue";
export default {
  name: "Home",
  components: { navigationBar },
  data() {
    return {
      timeValue: "",
      imgSrcTow: require("@/assets/95.jpg"),
      operationShow: false,
      date: "",
      month: "",
      year: "",
    };
  },

  watch: {
    route(to, from) {
      console.log(to); //从哪来
      console.log(to.path); //到哪去
    },
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      //获取当前时间并打印
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
      _this.date = dd >= 10 ? dd : "0" + dd;
      _this.month = mm == 12 ? "Dec." : 12;
      _this.year = yy;
    },
    dataMoChang() {
      this.$router.push({ path: "/" });
    },
    getDetails() {
      this.$router.push({ path: "/Details" });
    },
    operationChangeVlaue(val) {
      this.operationShow = val;
    },
  },
};
</script>
<style>
.background {
  width: 600px;
  height: 1024px;
  z-index: -1;
  position: absolute;
}
.fg {
  font-size: 5rem;
  color: #44484d;
  font-weight: 100;
  margin: 3.3rem 0.5rem 0;
}
.ez {
  font-size: 2rem;
  font-family: light;
  font-weight: 300;
  color: #44484d;
  margin-top: 3rem;
}
.ez span {
  display: block;
}
.monthVal {
  text-align: center;
  font-size: 1.43rem;
  font-family: regular;
  font-weight: 400;
  color: #44484d;
  padding: 1rem;
}
.van-button--info {
  width: 5.83rem;
  height: 2.5rem;
  background: #eeeeee !important;
  border-radius: 0rem;
  border-color: #eeeeee !important;
  color: #44484d !important;
}
.operation {
  position: fixed;
  top: 0;
  z-index: 1;
}
.operation .tab {
  font-family: regular;
  font-size: 4rem;
  font-weight: 400;
  color: #434343;
  padding: 0;
  margin: 0 3rem 0;
  position: absolute;
  z-index: 1;
  right: 0rem;
  top: 0;
}
</style>
