<template>
  <div style="width:600px;height:1024px">
    <div class="posterBg">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>
    <div class="front">
      <navigationBar
        :timeValue="timeValue"
        @operationChangeVlaue="operationChangeVlaue"
        v-if="operationShow"
      ></navigationBar>
      <div class="tab-top">
        <van-icon
          @click="operationShow = true"
          name="wap-nav"
          style="align-items: center;justify-content: center;display: flex;"
        />
        <span style="font-family: regular;font-weight: 400;"
          >{{ timeValue }}
        </span>
      </div>
      <img :src="spectrumSrc" alt="" class="spectrum" />
      <img :src="loadingSrc" alt="" style="position: absolute;top: 0rem;z-index: -1;"/>
    </div>
  </div>
</template>
<script>
import navigationBar from "../components/navigationBar.vue";
export default {
  components: { navigationBar },
  data() {
    return {
      timeValue: "",
      loadingSrc: require("@/assets/hb.gif"),
      imgSrc: require("@/assets/informa.jpg"),
      spectrumSrc: require("@/assets/spectrum.gif"),
      operationShow: false,
    };
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
    },
    operationChangeVlaue(val) {
      this.operationShow = val;
    },
  },
};
</script>