import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Loaded from '../views/loading.vue'
import Details from '../views/details.vue'
import Poster from '../views/poster.vue'
import PosterLoad from '../views/posterLoad.vue'
import InformalEssay from '../views/informalEssay.vue'
import InformalEssayLoad from '../views/informalEssayLoad.vue'
import LittleBlueMan from '../views/littleBlueMan.vue'
import Week from '../views/week.vue'
import Awaken from '../views/awaken.vue'


Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:"/Loaded",
    name:Loaded,
    component:Loaded
  },
  // 周历详情
  {
    path:"/Details",
    name:Details,
    component:Details
  },
  // 一键海报播放页
  {
    path:"/Poster",
    name:Poster,
    component:Poster
  },
  // 一键海报播loading
  {
    path:"/PosterLoad",
    name:PosterLoad,
    component:PosterLoad
  },
  //AI信息页
  {
    path:"/InformalEssay",
    name:InformalEssay,
    component:InformalEssay
  },
  //AIloading
  {
    path:"/InformalEssayLoad",
    name:InformalEssayLoad,
    component:InformalEssayLoad
  },
  // 初次小蓝人
  {
    path:"/LittleBlueMan",
    name:LittleBlueMan,
    component:LittleBlueMan
  },
  {
    path:"/Week",
    name:Week,
    component:Week
  },
  // 我在听
  {
    path:"/Awaken",
    name:Awaken,
    component:Awaken
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
